<template>
  <div class="abnormalDetails full">
    <head-top headTitle = "" noneIcon = "true">
        <div slot="save" @click = "$router.go(-1)"></div>
    </head-top>
    <div class="section flex_col_left">
        <div class="con-list-sm padding">
            <light-cell lfCon = "" :rtCon = "pole.dgmc"></light-cell>
            <light-cell lfCon = "/" :rtCon = "pole.ldbmc"></light-cell>
            <light-cell lfCon = "" :rtCon = "pole.ycxx"></light-cell>
            <light-cell lfCon = "" :rtCon = "pole.ycsj"></light-cell>
            <light-cell lfCon = "" rtCon = "2018-10-10 19:20:11"></light-cell>
            <light-cell lfCon = "" rtCon = ''></light-cell>
            <div class="text-box">
                <div class="padding_tb flex_between"><div></div></div>
                <textarea name="remarks" id="remarks" placeholder=""></textarea>
            </div>
        </div>
        <div class = 'padding padding_tb' style="position: fixed;bottom: 0;width: 100%">
            <div class = 'btn_submit padding_tb'></div>
        </div>
    </div> 
  </div>
</template>

<script>
import headTop from '@/components/header/mainHeader'
import lightCell from '@/components/common/lightCell'

export default {
    components:{
        headTop,
        lightCell,
    },
    data () {
        return {
            pole:{  
                'dgmc':'1002',
                'yccs':15,
                'ldbmc':'',
                'ycxx':'',
                'ycsj':'2018-10-10 19:23:22',
                'yczt':''
            },
            ycArr:[{
                'ycxx':'',
                'ycsj':'2018-10-10 19:23:22',
            },{
                'ycxx':'',
                'ycsj':'2018-10-10 19:23:22',
            }]
        }
    }
}
</script>

<style scoped lang = "scss">
#remarks{
    width: 10.2rem !important;
    height: 3rem !important ;
    background:rgba(249,249,249,1);
    border-radius:0.01rem;
    text-align: left;
    font-size:0.42rem;
}
.text-box{
    padding-bottom: 0.3rem;
}
.btn_submit{
    width: 100%;
    background: #169AFF;
    color: white;
    border-radius: 0.05rem;
}
</style>



